<template>
    <div class="sign-up">
    
        <i-form :model="formInline"
                :rules="rulerInline"
                :label-width="100">
            <Form-item>
                <span class="title"><i class="fa fa-envelope-o fa-fw"></i> 邮箱注册</span>
            </Form-item>
            <Form-item label="填写常用邮箱"
                       prop="email">
                <i-input :value.sync="formInline.email"
                         placeholder="example@example.com">
                </i-input>
            </Form-item>
            <Form-item label="验证码"
                       prop="vertifyCode">
                <i-input :value.sync="formInline.vertifyCode"
                         placeholder="请输入">
                    <i-button slot="append"
                              @click="getCode()"
                              :type="hasSendCode?'':'primary'"
                              :disabled="hasSendCode">{{hasSendCode?'验证码已经发送':'发送验证码'}}</i-button>
                </i-input>
            </Form-item>
    
            <Form-item label="填写密码"
                       type="password"
                       prop="password">
                <i-input :value.sync="formInline.password"
                         placeholder="请输入">
                </i-input>
            </Form-item>
            <Form-item>
                <i-button type="primary">注册</i-button>
            </Form-item>
        </i-form>
    </div>
</template>

<script>
import { getVertifyCodeAction } from '../../vuex/actions'

export default {
    data() {
        return {
            hasSendCode: false,
            formInline: {
                email: "888",
                vertifyCode: "",
                password: ""
            },
            rulerInline: {
                email: [
                    { required: true, message: '请填写邮箱', trigger: 'blur' }
                ],
                vertifyCode: [
                    { required: true, message: '请填写邮箱验证码', trigger: 'blur' },
                ],
                password: [
                    { required: true, message: '请填写密码', trigger: 'blur' },
                ]
            }

        };
    },

    methods: {
        getCode() {
            var email = this.formInline.email;
            if (!email)
                return "";
            getVertifyCodeAction(email).then(res => {
                if (res.ok) {
                    this.hasSendCode = true;
                }
            }).catch(err => {

            })
        }
    },
    vuex: {
        getters: {

        },
        actions: {
            getVertifyCodeAction
        }
    }
}

</script>

<style lang="less">
.sign-up {
    margin: 0 auto;
    margin-top: 50px;
    width: 40%;
    min-width: 300px;
    .title {
        font-size: 18px;
    }
}
</style>


